document.addEventListener('DOMContentLoaded', async function() {
    console.log('DOM,');
    await initializeReviewPage();
    // 
    initializeFeedbackButton();
    
    
    // 
    const navButtons = document.querySelectorAll('.nav-btn');
    console.log(':', navButtons.length);
    
    // 
    const views = document.querySelectorAll('.view');
    console.log(':', views.length);
    
    // ID
    views.forEach(view => console.log('ID:', view.id));
    
    navButtons.forEach((button, index) => {
        console.log(` ${index + 1} :`, button.textContent);
        
        button.addEventListener('click', async function(e) {
            e.preventDefault(); // 
            e.stopPropagation(); // 
            
            console.log(':', this.textContent);
            
            // 
            navButtons.forEach(btn => btn.classList.remove('active'));
            // 
            this.classList.add('active');
            
            // 
            const targetView = this.textContent.trim();
            console.log(':', targetView);
            
            let viewId;
            switch(targetView) {
                case 'Review':
                    viewId = 'reviewView';
                    await initializeReviewPage();
                    break;
                case 'Problems':
                    viewId = 'problemListView';
                    await loadProblemList(); // 
                    initializeFeedbackButton();
                    // renderAll();
                    break;
                case 'Settings':
                    viewId = 'moreView';
                    await initializeOptions();
                    break;
            }
            
            console.log('ID:', viewId);
            
            // 
            views.forEach(view => {
                console.log(':', view.id);
                if(view.id === viewId) {
                    view.classList.add('active');
                    view.style.display = 'block';
                    console.log(':', view.id);
                } else {
                    view.classList.remove('active');
                    view.style.display = 'none';
                    console.log(':', view.id);
                }
            });
        });
    });

    //  revlogs
    try {
        console.log('=====  revlogs =====');
        const allRevlogs = await getAllRevlogs();
        console.log(':', allRevlogs);
        
        // 
        let totalReviews = 0;
        Object.keys(allRevlogs).forEach(cardId => {
            totalReviews += allRevlogs[cardId]?.length || 0;
        });
        console.log(`: ${totalReviews}`);
        
        //  CSV 
        const csvContent = await exportRevlogsToCSV();
        console.log('CSV :');
        console.log(csvContent);
        console.log('=====  revlogs =====');
    } catch (error) {
        console.error(' revlogs :', error);
    }
});








